/*       */
import express from 'express';
import logger from 'morgan';
import cookieParser from 'cookie-parser';
import bodyParser from 'body-parser';
import mongoose from 'mongoose';
import debug from 'debug';
                                      
                                                         
import PostRouter from './routes/PostRouter';

class Application {
                               
                        
                    
                  
                    

  constructor() {
    this.DEFAULT_PORT = 3000;
    this.port = this.normalizePort(process.env.PORT || '3000');
    this.mongodURI = process.env.MONGODB_URI || 'mongodb://127.0.0.1/tourismatik';
    this.express = express();
    this.debug = debug('tourismatik:server');
    this.dbConnect();
    this.middleware();
    this.routes();
    this.start();
  }

  normalizePort(val        )                  {
    const normalizedport = parseInt(val, 10);

    if (isNaN(normalizedport)) {
      // named pipe
      return val;
    }

    if (normalizedport >= 0) {
      // port number
      return normalizedport;
    }

    return this.DEFAULT_PORT;
  }

  onListening()       {
    const addr                     = this.express.address();
    const bind         = typeof addr === 'string'
      ? `pipe ${addr}`
      : `pipe ${addr.port}`;
    this.debug(`Listening on + ${bind}`);
  }

  onError(error             )       {
    if (error.syscall !== 'listen') {
      throw error;
    }
    const bind         = typeof this.port === 'string'
      ? `Pipe ${this.port}`
      : `Port ${this.port.toString()}`;

    switch (error.code) {
      case 'EACCES':
        console.error(`${bind} requires elevated privileges`);
        process.exit(1);
        break;
      case 'EADDRINUSE':
        console.error(`${bind} is already in use`);
        process.exit(1);
        break;
      default:
        throw error;
    }
  }

  dbConnect()       {
    mongoose.Promise = global.Promise;
    mongoose.connect(this.mongodURI, { useMongoClient: true }, (err) => {
      if (err) {
        console.error(err);
      }
    });
  }
  middleware()       {
    this.express.use(logger('dev'));
    this.express.use(bodyParser.json());
    this.express.use(bodyParser.urlencoded({ extended: false }));
    this.express.use(cookieParser());
    /**
     * Error Handler
     */
    this.express.use((error        , req          , res           , next              ) => {
      if (error) {
        res.locals.message = error.message;
        res.locals.error = req.app.get('env') === 'development' ? error : {};
        res.status(error.status || 500);
        res.json({ error: error.message });
      }
    });
  }

  routes()       {
    const postRouter = new PostRouter();
    this.express.use(postRouter.path, postRouter.router);
    this.express.use((req          , res           , next              )       => {
      const err              = new Error('Not Found');
      err.status = 404;
      next(err);
    });
  }

  start() {
    this.express.listen(this.port);
    this.express.on('error', this.onError);
    this.express.on('listening', this.onListening);
  }
}

const app = new Application();
export default app;



