import { Router } from 'express';
import postModel from '../models/post';

export default class PostRouter {
                 
               

  constructor(path         = '/api')       {
    this.router = Router();
    this.path = path;
    this.init();
  }

  static placeholder(req          , res           ) {
    res.status(200).json({
      success: true,
    });
  }

  init()       {
    this.router.get('/', PostRouter.placeholder);
  }
}



